.grid-container {
    display: flex;
    justify-content: center;
    align-items: center;
    /* max-width: 720px; */
    height: 100%;
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
}

.grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 500px;
    /* min-width: 400px; */
}

.tiles-parent:nth-child(1) {
    border-right: 5px solid white;
    border-bottom: 5px solid white;
}
.tiles-parent:nth-child(2) {
    border-bottom: 5px solid white;
}
.tiles-parent:nth-child(3) {
    border-left: 5px solid white;
    border-bottom: 5px solid white;
}

.tiles-parent:nth-child(4) {
    border-right: 5px solid white;
}

.tiles-parent:nth-child(6) {
    border-left: 5px solid white;
}
.tiles-parent:nth-child(7) {
    border-top: 5px solid white;
    border-right: 5px solid white;
}
.tiles-parent:nth-child(8) {
    border-top: 5px solid white;
}
.tiles-parent:nth-child(9) {
    border-top: 5px solid white;
    border-left: 5px solid white;
}

.winner {
    position: fixed;
    font-size: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 0;
    width: 100%;
    color: red;
}

@media (max-width: 550px) { 
    .grid {
        width: 350px;
    }
}

@media (max-width: 315px) {
    .grid {
        width: 300px;
    }
}